import { useCallback, useEffect, useMemo, useState } from 'react';
import {
  ICreateModalContentsProps,
  ICreateModalContentsState,
} from './CreateModalContents.types';
import {
  DataState,
  combineStates,
  getLogger,
  isDataStateLoading,
  useArtifactClient,
} from '@do/core';
import {
  CreateArtifactParams,
  CreateArtifactResult,
  GetArtifactResult,
  UpdateArtifactParams,
  UpdateArtifactResult,
} from '@trident/data-access-artifact';
import { usePrevious } from '@fluentui/react-hooks';

const MODULE_NAME = 'OperationalInsightsCreate';

export const useCreateModalContentsState = (
  props: ICreateModalContentsProps,
): ICreateModalContentsState => {
  const { artifactId, isOpen, onClose, workspaceId: workspaceIdProp } = props;
  const mode = artifactId ? 'edit' : 'create';
  // contexts

  // state
  const [getArtifactResult, setGetArtifactResult] = useState<
    DataState<GetArtifactResult | undefined>
  >({
    state: 'NotCalled',
  });
  const [createArtifactResult, setCreateArtifactResult] = useState<
    DataState<CreateArtifactResult | undefined>
  >({
    state: 'NotCalled',
  });
  const [updateArtifactResult, setUpdateArtifactResult] = useState<
    DataState<UpdateArtifactResult | undefined>
  >({
    state: 'NotCalled',
  });
  const [artifactName, setArtifactName] = useState<string>('');

  // hooks
  const artifactClient = useArtifactClient();

  // callbacks
  const isSubmitEnabled =
    artifactName?.trim().length > 0 && // name entered
    !isDataStateLoading(
      createArtifactResult,
      updateArtifactResult,
      getArtifactResult,
    ) && // not creating, updating, fetching
    !(createArtifactResult?.data || updateArtifactResult?.data); // haven't already saved

  const onSubmit = useCallback(async () => {
    if (mode === 'edit') {
      const artifactUpateParams: UpdateArtifactParams = {
        etag: getArtifactResult?.data?.etag || '',
        payload: {
          ...getArtifactResult,
          displayName: artifactName,
        },
        objectId: artifactId,
      };
      getLogger().debug(
        MODULE_NAME,
        `Updating artifact {payload}`,
        artifactUpateParams,
      );
      setUpdateArtifactResult({ state: 'Loading' });
      const result = await artifactClient?.updateArtifact(artifactUpateParams);
      if (result) {
        setUpdateArtifactResult(result);
      }
    } else {
      const args: CreateArtifactParams = {
        workspaceObjectId: workspaceIdProp,
        payload: {
          artifactType: 'DigitalOperationsOperationalInsight',
          displayName: artifactName,
        },
      };

      getLogger().debug(MODULE_NAME, `Create artifact {payload}`, args);
      setCreateArtifactResult({ state: 'Loading' });
      const result = await artifactClient?.createArtifact(args);
      if (result) {
        setCreateArtifactResult(result);
      }
    }
  }, [
    artifactClient,
    artifactId,
    artifactName,
    getArtifactResult,
    mode,
    workspaceIdProp,
  ]);

  // side effects
  const previousOpen = usePrevious(isOpen);
  // when the dialog is closed, reset the state
  useEffect(() => {
    if (!isOpen && previousOpen) {
      setGetArtifactResult({ state: 'NotCalled' });
      setCreateArtifactResult({ state: 'NotCalled' });
      setUpdateArtifactResult({ state: 'NotCalled' });
    }
  }, [isOpen, previousOpen]);

  // when the dialog is opened, if we are in edit mode, get the artifact details
  useEffect(() => {
    if (isOpen && mode === 'edit' && isOpen !== previousOpen) {
      const fetchArtifact = async () => {
        getLogger().debug(
          MODULE_NAME,
          `Fetching artifact with id: ${artifactId}`,
        );
        setGetArtifactResult({ state: 'Loading' });
        const result = await artifactClient?.getArtifact({
          objectId: artifactId,
        });
        if (result) {
          setGetArtifactResult(result);
        }
        getLogger().debug(
          MODULE_NAME,
          `Fetched artifact with id: ${artifactId}`,
        );
      };
      fetchArtifact();
    }
  }, [artifactClient, artifactId, isOpen, mode, previousOpen]);

  // if the call completed, close the dialog
  useEffect(() => {
    if (
      createArtifactResult.state === 'Complete' &&
      createArtifactResult.data
    ) {
      onClose('submit', {
        objectId: createArtifactResult.data.objectId,
        displayName: createArtifactResult.data.displayName,
      });
    }
  }, [createArtifactResult, onClose]);

  // if the call completed, close the dialog
  useEffect(() => {
    if (
      updateArtifactResult.state === 'Complete' &&
      updateArtifactResult.data
    ) {
      onClose('submit', {
        objectId: updateArtifactResult.data.objectId,
        displayName: updateArtifactResult.data.displayName,
      });
    }
  }, [updateArtifactResult, onClose]);

  // data
  const saveError = createArtifactResult.error || updateArtifactResult.error;
  const saveState = combineStates(
    createArtifactResult.state,
    updateArtifactResult.state,
  );

  // result
  const state: ICreateModalContentsState = useMemo(() => {
    return {
      saveError,
      setArtifactName,
      artifactName,
      onClose,
      isSubmitEnabled,
      onSubmit,
      saveState,
      getArtifactResult,
    };
  }, [
    artifactName,
    getArtifactResult,
    isSubmitEnabled,
    onClose,
    onSubmit,
    saveError,
    saveState,
  ]);

  return state;
};
