import { useMemo } from 'react';
import { IHomePageState } from './HomePage.types';
import { ExtensionRoutes, useAppRouteProps, useNavigation } from '@do/core';

export const useHomePageState = (): IHomePageState => {
  // contexts

  // state

  // hooks
  const mainRouteProps = useAppRouteProps();
  const navigation = useNavigation();

  // callbacks

  // side effects

  // result
  const state: IHomePageState = useMemo(() => {
    return {
      navigateToMappingPage: () => {
        navigation?.navigate({
          path: `${ExtensionRoutes.prepare(mainRouteProps.artifactId)}/mapping`,
        });
      },
      navigateToContextualizationPage: () => {
        navigation?.navigate({
          path: `${ExtensionRoutes.prepare(
            mainRouteProps.artifactId,
          )}/contextualize`,
        });
      },
      navigateToModelingPage: () => {
        navigation?.navigate({
          path: `${ExtensionRoutes.prepare(mainRouteProps.artifactId)}/models`,
        });
      },
      navigateToExplorer: () => {
        navigation?.navigate({
          path: ExtensionRoutes.explorer(mainRouteProps.artifactId),
        });
      },
      navigateToCalcEngine: () => {
        navigation?.navigate({
          path: ExtensionRoutes.insights(mainRouteProps.artifactId),
        });
      },
    };
  }, [navigation, mainRouteProps.artifactId]);

  return state;
};
