import { HomeSectionStyles, HomeSectionClasses } from './HomeSection.types';
import { makeStyles } from '@fluentui/react-components';

export const useClassNames: () => HomeSectionStyles =
  makeStyles<HomeSectionClasses>({
    root: {},
    row: {
      display: 'flex',
      flexDirection: 'row',
      justifyContent: 'start',
      alignItems: 'center',
      columnGap: '20px',
    },
    aside: {
      paddingBottom: '20px',
    },
  });
