import React from 'react';
import { HomeSectionProps } from './HomeSection.types';
import { useHomeSectionRender } from './HomeSection.render';
import { useClassNames } from './HomeSection.styles';
import { useHomeSectionState } from './HomeSection.state';

const MODULE_NAME = 'HomeSection';

export const HomeSection: React.FC<HomeSectionProps> = (props) => {
  // get state
  const state = useHomeSectionState(props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useHomeSectionRender({
    state,
    styles,
    children: props.children,
    className: props.className,
  });
};

HomeSection.displayName = MODULE_NAME;
