import React from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import { ICreateDialogProps } from './CreateDialog.types';
import { useCreateDialogRender } from './CreateDialog.render';
import { useClassNames } from './CreateDialog.styles';
import { useCreateDialogState } from './CreateDialog.state';

const MODULE_NAME = 'CreateDialog';

export const CreateDialog: React.FC<ICreateDialogProps> = (_props) => {
  // get state
  const state = useCreateDialogState(_props);
  const debugLogging = useDebugLogging();

  // get styles
  const styles = useClassNames();

  debugLogging &&
    getLogger().debug('Render. {state, styles}', MODULE_NAME, {
      state,
      styles,
    });

  // render the item
  return useCreateDialogRender({ state, styles });
};

CreateDialog.displayName = MODULE_NAME;
