import React from 'react';
import { IHomePageRenderProps } from './HomePage.types';
import { ExternalLink, Page } from '@do/components';
import { HomeSection } from '../../components/HomeSection';
import { t, strings } from '@do/localization';

export const TestIds = {
  MappingJobs: 'Homepage-mapping-jobs-button',
};

export const useHomePageRender: React.FC<IHomePageRenderProps> = (props) => {
  const { state, styles } = props;

  return (
    <Page>
      <div className={styles.root}>
        <h1>{t(strings.home.title)}</h1>
        <HomeSection
          title={t(strings.home.prepare.title)}
          actions={[
            {
              key: 'mapping',
              className: styles.card,
              title: t(strings.home.prepare.cards.mapping.title),
              action: {
                onClick: state.navigateToMappingPage,
                children: t(strings.home.prepare.cards.mapping.button),
              },
              children: t(strings.home.prepare.cards.mapping.description),
            },
            {
              key: 'contextualization',
              className: styles.card,
              title: t(strings.home.prepare.cards.contextualization.title),
              action: {
                onClick: state.navigateToContextualizationPage,
                children: t(
                  strings.home.prepare.cards.contextualization.button,
                ),
              },
              children: t(
                strings.home.prepare.cards.contextualization.description,
              ),
            },
            {
              key: 'modeling',
              className: styles.card,
              title: t(strings.home.prepare.cards.grouping.title),
              action: {
                onClick: state.navigateToModelingPage,
                children: t(strings.home.prepare.cards.grouping.button),
              },
              children: t(strings.home.prepare.cards.grouping.description),
            },
          ]}
        >
          <ExternalLink
            akaShortLink='PAL-overview-prepare'
            contentBefore={t(strings.home.prepare.description)}
            text={t(strings.common.learn_more)}
          />
        </HomeSection>
        <HomeSection
          title={t(strings.home.explorer.title)}
          actions={[
            {
              key: 'explorer',
              className: styles.card,
              title: t(strings.home.explorer.cards.explorer.title),
              action: {
                onClick: state.navigateToExplorer,
                children: t(strings.home.explorer.cards.explorer.button),
              },
              children: t(strings.home.explorer.cards.explorer.description),
            },
          ]}
        >
          <ExternalLink
            akaShortLink='PAL-explorer'
            contentBefore={t(strings.home.explorer.description)}
            text={t(strings.common.learn_more)}
          />
        </HomeSection>
        <HomeSection
          title={t(strings.home.calc_engine.title)}
          actions={[
            {
              key: 'calc-engine',
              className: styles.card,
              title: t(strings.home.calc_engine.cards.formulas.title),
              action: {
                onClick: state.navigateToCalcEngine,
                children: t(strings.home.calc_engine.cards.formulas.button),
              },
              children: t(strings.home.calc_engine.cards.formulas.description),
            },
          ]}
        >
          <ExternalLink
            akaShortLink='PAL-formulas'
            contentBefore={t(strings.home.calc_engine.description)}
            text={t(strings.common.learn_more)}
          />
        </HomeSection>
      </div>
    </Page>
  );
};
