import React from 'react';
import { SectionCardProps } from './SectionCard.types';
import { useSectionCardRender } from './SectionCard.render';
import { useClassNames } from './SectionCard.styles';
import { useSectionCardState } from './SectionCard.state';

const MODULE_NAME = 'SectionCard';

export const SectionCard: React.FC<SectionCardProps> = (props) => {
  // get state
  const state = useSectionCardState(props);

  // get styles
  const styles = useClassNames();

  // render the item
  return useSectionCardRender({
    state,
    styles,
    children: props.children,
    className: props.className,
  });
};

SectionCard.displayName = MODULE_NAME;
