import React from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import { ICreateModalContentsProps } from './CreateModalContents.types';
import { useCreateModalContentsRender } from './CreateModalContents.render';
import { useClassNames } from './CreateModalContents.styles';
import { useCreateModalContentsState } from './CreateModalContents.state';

const MODULE_NAME = 'CreateModalContents';

export const CreateModalContents: React.FC<ICreateModalContentsProps> = (
  props,
) => {
  // get state
  const state = useCreateModalContentsState(props);
  const debugLogging = useDebugLogging();

  // get styles
  const styles = useClassNames();

  debugLogging &&
    getLogger().debug('Render. {state, styles}', MODULE_NAME, {
      state,
      styles,
    });

  // render the item
  return useCreateModalContentsRender({ state, styles });
};

CreateModalContents.displayName = MODULE_NAME;
