import {
  ICreateModalContentsStyles,
  CreateModalContentsClasses,
} from './CreateModalContents.types';
import { makeStyles, shorthands } from '@fluentui/react-components';

export const useClassNames: () => ICreateModalContentsStyles =
  makeStyles<CreateModalContentsClasses>({
    root: {
      display: 'flex',
      height: '100%',
      flexDirection: 'column',
      maxHeight: '100%',
      ...shorthands.padding('16px'),
      ...shorthands.gap('8px'),
    },
    nameField: {
      marginBottom: 'auto',
    },
    actions: {
      display: 'flex',
      justifyContent: 'flex-end',
      ...shorthands.gap('8px'),
    },
    saveButton: {
      display: 'flex',
      ...shorthands.gap('4px'),
    },
  });
