import React from 'react';
import { ICreateModalContentsRenderProps } from './CreateModalContents.types';
import { getTridentAPIErrorMessage } from '@do/core';
import { t, strings } from '@do/localization';
import {
  DialogBody,
  Subtitle2,
  Field,
  Input,
  Button,
  Spinner,
  MessageBar,
} from '@fluentui/react-components';

const enum TestIds {
  Root = 'create-modal-root',
  NameInput = 'create-modal-name-input',
  CancelButton = 'create-modal-cancel-button',
  SaveButton = 'create-modal-save-button',
  ErrorBanner = 'create-modal-error-banner',
}

export const useCreateModalContentsRender: React.FC<
  ICreateModalContentsRenderProps
> = (props) => {
  const { state, styles } = props;

  const isCreating = state.saveState === 'Loading';
  return (
    <DialogBody className={styles.root} data-testid={TestIds.Root}>
      <Subtitle2>
        {t(strings.artifacts.operational_insights.create.title_create)}
      </Subtitle2>
      {state.saveError && (
        <MessageBar intent='error' data-testid={TestIds.ErrorBanner}>
          {getTridentAPIErrorMessage(state.saveError)}
        </MessageBar>
      )}
      <Field
        className={styles.nameField}
        label={t(
          strings.artifacts.operational_insights.create.artifact_name_label,
        )}
        orientation='vertical'
      >
        <Input
          onChange={(_ev, data) => state.setArtifactName(data.value)}
          placeholder={
            t(
              strings.artifacts.operational_insights.create
                .artifact_name_placeholder,
            ) as string
          }
          data-testid={TestIds.NameInput}
          value={state.artifactName}
        />
      </Field>
      <div className={styles.actions}>
        <Button
          onClick={() => state.onClose('cancel')}
          data-testid={TestIds.CancelButton}
        >
          {t(strings.common.cancel)}
        </Button>
        <Button
          appearance='primary'
          className={styles.saveButton}
          disabled={!state.isSubmitEnabled}
          onClick={state.onSubmit}
          data-testid={TestIds.SaveButton}
        >
          {isCreating && <Spinner size='tiny' />}
          {isCreating ? t(strings.common.creating) : t(strings.common.create)}
        </Button>
      </div>
    </DialogBody>
  );
};
