import { useMemo } from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import { SectionCardProps, SectionCardState } from './SectionCard.types';
import { resolveShorthand } from '@fluentui/react-components';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'SectionCard.state';

export const useSectionCardState = (
  props: SectionCardProps,
): SectionCardState => {
  // contexts

  // state

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);

  // callbacks

  // side effects

  // result
  const state = useMemo(() => {
    return {
      components: {
        title: 'h3',
        icon: 'div',
        action: 'button',
      },
      title: resolveShorthand(props.title),
      icon: resolveShorthand(props.icon),
      action: resolveShorthand(props.action),
    };
  }, [props.action, props.icon, props.title]);

  debugLogging &&
    getLogger().debug(MODULE_NAME, 'Render. {state}', {
      state,
    });

  return state as SectionCardState;
};
