import React from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import { useHomePageRender } from './HomePage.render';
import { useClassNames } from './HomePage.styles';
import { useHomePageState } from './HomePage.state';

const MODULE_NAME = 'HomePage';

export const HomePage: React.FC = () => {
  // get state
  const state = useHomePageState();
  const debugLogging = useDebugLogging();

  // get styles
  const styles = useClassNames();

  debugLogging &&
    getLogger().debug('Render. {state, styles}', MODULE_NAME, {
      state,
      styles,
    });

  // render the item
  return useHomePageRender({ state, styles });
};

HomePage.displayName = MODULE_NAME;
