import { IHomePageStyles, HomePageClasses } from './HomePage.types';
import { makeStyles } from '@fluentui/react-components';

export const useClassNames: () => IHomePageStyles = makeStyles<HomePageClasses>(
  {
    root: {
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'start',
      alignItems: 'start',
      rowGap: '10px',
    },
    rotate180: {
      transform: 'rotate(180deg)',
    },
    card: {
      width: '25vw',
      maxWidth: '418px',
      minHeight: '160px', //make all same height
    },
  },
);
