import React from 'react';
import {
  HomeSectionAction,
  HomeSectionRenderProps,
  HomeSectionSlots,
} from './HomeSection.types';
import { getSlots, mergeClasses } from '@fluentui/react-components';
import { SectionCard } from './SectionCard';

export const useHomeSectionRender: React.FC<HomeSectionRenderProps> = ({
  state,
  styles,
  children,
  className,
}) => {
  const { slots, slotProps } = getSlots<HomeSectionSlots>(state);
  return (
    <section role='group' className={mergeClasses(styles.root, className)}>
      <slots.title {...slotProps.title} />
      <aside className={styles.aside}>{children}</aside>
      <section className={styles.row} role='group'>
        {state.actions.map(renderSectionCard)}
      </section>
    </section>
  );
};

const renderSectionCard = (action: HomeSectionAction) => {
  return (
    <SectionCard
      className={action.className}
      key={action.key}
      title={action.title}
      icon={action.icon}
      action={action.action}
    >
      {action.children}
    </SectionCard>
  );
};
