import React from 'react';
import { ICreateDialogRenderProps } from './CreateDialog.types';
import { CreateModalContents } from '../../components';

export const useCreateDialogRender: React.FC<ICreateDialogRenderProps> = (
  props,
) => {
  const { state, styles } = props;

  return (
    <div className={styles.root}>
      <CreateModalContents
        artifactId='' // always create mode
        isOpen={state.isOpen}
        onClose={state.onDialogClose}
        workspaceId={state.workspaceObjectId}
      />
    </div>
  );
};
