import React from 'react';
import { SectionCardRenderProps, SectionCardSlots } from './SectionCard.types';
import {
  Body1,
  Button,
  Card,
  CardFooter,
  CardHeader,
  getSlots,
  mergeClasses,
} from '@fluentui/react-components';

export const useSectionCardRender: React.FC<SectionCardRenderProps> = ({
  state,
  styles,
  children,
  className,
}) => {
  const { slots, slotProps } = getSlots<SectionCardSlots>(state);

  return (
    <Card className={mergeClasses(styles.root, className)}>
      <CardHeader
        header={
          <Body1>
            <h3 className={styles.title} {...slotProps.title} />
          </Body1>
        }
        action={slots.icon ? <slots.icon {...slotProps.icon} /> : null}
      />
      <div className={styles.body}>{children}</div>
      <CardFooter className={styles.footer}>
        {slots.action !== undefined ? (
          <Button {...slotProps.action} ref={undefined} />
        ) : null}
      </CardFooter>
    </Card>
  );
};
