import { useMemo } from 'react';
import { getLogger, useDebugLogging } from '@do/core';
import { HomeSectionProps, HomeSectionState } from './HomeSection.types';
import { resolveShorthand } from '@fluentui/react-components';

const DEBUG_LOGGING = false;
const MODULE_NAME = 'HomeSection.state';

export const useHomeSectionState = (
  props: HomeSectionProps,
): HomeSectionState => {
  // contexts

  // state

  // hooks
  const debugLogging = useDebugLogging(DEBUG_LOGGING);

  // callbacks

  // side effects

  // result
  const state: HomeSectionState = useMemo(() => {
    return {
      actions: props.actions,
      components: {
        title: 'h2',
      },
      title: resolveShorthand(props.title),
    };
  }, [props.actions, props.title]);

  debugLogging &&
    getLogger().debug(MODULE_NAME, 'Render. {state}', {
      state,
    });

  return state;
};
