import { useCallback, useMemo } from 'react';
import {
  ICreateDialogProps,
  ICreateDialogState,
  ISimpleArtifact,
  QueryStringParams,
} from './CreateDialog.types';
import {
  ExtensionRoutes,
  SearchParams,
  getLogger,
  useNavigation,
  useURLQueryParms,
} from '@do/core';

const MODULE_NAME = 'CreateDialog';

export const useCreateDialogState = (
  props: ICreateDialogProps,
): ICreateDialogState => {
  const qsp = useURLQueryParms<QueryStringParams>();
  const workspaceObjectId = qsp?.[SearchParams.WORKSPACE_OBJECT_ID];
  // contexts

  // state

  // hooks
  const navigation = useNavigation();

  // callbacks
  const onDialogClose = useCallback(
    async (action: 'cancel' | 'submit', artifact?: ISimpleArtifact) => {
      if (action === 'submit' && artifact) {
        getLogger().info(
          MODULE_NAME,
          `Create dialog closing. Navigating to edit page for artifact ${artifact.objectId}`,
        );
        await navigation?.closeDialog();
        // navigate to the page with the new artifact
        navigation?.navigate({
          path: ExtensionRoutes.home(artifact.objectId),
        });
      } else {
        getLogger().info(MODULE_NAME, `Create dialog closing. Cancelled.`);
        await navigation?.closeDialog();
      }
    },
    [navigation],
  );

  // side effects

  // result
  const state: ICreateDialogState = useMemo(() => {
    return {
      isOpen: props.isOpen,
      onDialogClose,
      workspaceObjectId,
    };
  }, [onDialogClose, props.isOpen, workspaceObjectId]);

  return state;
};
